<template>
  <div class="add-task-page">
    <!--  -->
    <div class="addtask-set-item-box">
      <p class="set-title"></p>
      <p
        class="set-choice"
        v-for="item in typeList"
        :key="item.id"
        :class="{ select: taskType === item.id }"
        @click="taskType = item.id"
      >
        {{ item.text }}
      </p>
    </div>
    <!--  -->
    <div class="addtask-set-item-box">
      <p class="set-title"></p>
      <div class="stars-box">
        <Stars
          class="stars"
          :isCLickActive="true"
          :num="oldaign"
          :size="20"
          @getStarsnum="getStarsnum"
        ></Stars>
      </div>
    </div>
    <!--  -->
    <div class="addtask-set-item-box">
      <p class="set-title"></p>
      <span
        v-for="item in stateList"
        class="state iconfont"
        :class="[item.id, { select: stateVal === item.id }]"
        :key="item.id"
        @click="stateVal = item.id"
        >{{ item.icon }} {{ item.text }}</span
      >
    </div>
    <!--  -->
    <div class="addtask-set-item-box">
      <input
        type="text"
        placeholder=""
        class="todo-input"
        @keyup.enter="addTaskFn"
        v-model.trim="matterTitle"
      />

      <textarea
        v-model.trim="matterContent"
        class="my-note"
        placeholder=""
      ></textarea>
    </div>
    <div class="addtask-set-item-box">
      <p class="add-task-btn" @click="addTaskFn"></p>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance
} from 'vue';
import { stateList, typeList } from './config.js';
export default {
  name: '',
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      stateList, //
      typeList, //
      taskType: 'work', //
      oldaign: 1, //
      stateVal: 'notStarted', // ,
      matterTitle: '', //
      matterContent: '' //
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    // 
    const getStarsnum = (newval) => {
      data.oldaign = newval;
    };
    const addTaskFn = () => {
      if (data.matterTitle) {
        const matterObj = {
          //
          matter_id: Date.now().toString(16), //id
          matter_date: new Date().toLocaleString(),
          matter_title: data.matterTitle, //
          matter_content: data.matterContent, //
          matter_grade: data.oldaign, //
          matter_label: data.taskType, //
          matter_state: data.stateVal //
        };
        console.log(matterObj);
        ctx.emit('addTask', matterObj); //
      } else {
        proxy.h_tips({
          type: 'info',
          msg: ' ',
          top: 30,
          time: 1000
        });
      }
    };
    return {
      addTaskFn,
      getStarsnum,
      ...toRefs(data)
    };
  }
};
</script>
<style lang="less" scoped>
.add-task-page {
  width: 100%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
  background: #ffffff;
  border: 1px solid #efefef;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.06);
  border-radius: 4px;
  .addtask-set-item-box {
    overflow: hidden;
    .set-title {
      float: left;
      color: #333;
      font-size: 14px;
      line-height: 30px;
    }
    .set-choice {
      font-size: 14px;
      line-height: 30px;
      float: left;
      margin-left: 10px;
      cursor: pointer;
      &.select {
        color: @TC_B;
        font-weight: 700;
      }
    }
    .stars-box {
      float: left;
      margin-left: 10px;
    }

    /*  */
    .state {
      float: left;
      cursor: pointer;
      line-height: 32px;
      margin: 0 10px;
      font-size: 14px;
      color: rgb(179, 179, 179);
      transition: 0.3s;
    }
    .state:hover,
    .state.select {
      font-weight: 700;
    }
    /*  */
    .notStarted:hover,
    .notStarted.select {
      color: #ff0000;
    }
    .conduct:hover,
    .conduct.select {
      color: #00bfff;
    }
    .complete:hover,
    .complete.select {
      color: #09ff00;
    }
    /*  */
    .todo-input {
      height: 40px;
      outline: none;
      border: 1px solid #eee;
      padding: 0 10px;
      border-radius: 5px;
      box-sizing: border-box;
      cursor: pointer;
      width: 100%;
      color: #000;
      transition: 0.3s;
      font-family: 'serif';
      &::-webkit-input-placeholder {
        font-size: 14px;
        color: #c0c4cc;
        font-family: 'serif';
      }
    }
    .my-note {
      margin-top: 10px;
      height: 570px;
      resize: none;
      border: 1px solid #eee;
      outline: none;
      padding: 10px;
      box-sizing: border-box;
      color: #000;
      width: 100%;
      background: #fff;
      outline: none;
      border-radius: 5px;
      font-family: 'serif';
      cursor: pointer;
      &::-webkit-input-placeholder {
        font-size: 14px;
        color: #c0c4cc;
        font-family: 'serif';
      }
      &::-webkit-scrollbar {
        width: 12px;
      }
      /*  */
      &::-webkit-scrollbar-track {
        border-radius: 10px;
        background: rgb(224, 224, 224);
      }
      /*  */
      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: rgb(189, 189, 189);
      }
    }
    .add-task-btn {
      float: right;
      padding: 0 10px;
      height: 30px;
      background: @TC_B;
      border-radius: 4px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      margin-top: 10px;
      line-height: 30px;
      transition: 0.1s;
      &:hover {
        cursor: pointer;
        opacity: 0.8;
      }
    }
  }
}
</style>
